.main-bg{
  height: calc(100vh - 56px);
  background: url('./img/main_bg.jpg') no-repeat center / cover;
}
.logo{
  width: 63px;
  height: 35px;
  background: url('./img/logo.svg') no-repeat center / cover;
}

.item-wrap{
  margin:30px auto;
  text-align: left;
}
.btn-more{
  padding: 10px 20px;
  border: 1px solid #ccc;
  border-radius: 50px;
  outline:0;
  background-color:#fff;
}

.item{
  flex: 1 0 25%;
}
.item:nth-child(n+5){
  margin-top: 20px;
}

.thumbnail-info{
  margin-top: 10px;
}

.product{
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
}

.product-note{
  margin-top: 8px;
  margin-bottom: 0;
  font-size: 12px;
}

.price{
  font-size: 14px;
}

.alert{
  width :100%;
  height : 50px;
  background: #fff;
  border:0;
}

.App {
  padding-bottom: 100px;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
